import { useState } from 'react';
import Form from 'react-bootstrap/Form';

export default function App() {

  let [red, setred] = useState(50)
  let [green, setgreen] = useState(50)
  let [blue, setblue] = useState(50)

  function changeColor() {
    red =  setred(document.getElementById('red').value)
    green = setgreen(document.getElementById('green').value)
    blue = setblue(document.getElementById('blue').value)
  }
  document.body.style.backgroundColor = `rgb(${red}, ${green}, ${blue})`

  return (
    <div className="App">
      {/* Card */}
      <div style={{
        width: '10rem',
        padding: '10px',
        transition: '0.3s',
        textAlign: 'center',
        backgroundColor: 'white',
        boxShadow: '0 4px 8px 0 rgba(0,0,0,0.5)',
      }}>
        <Form>
          <Form.Group controlId="formBasicRange">
            <Form.Label>Red :</Form.Label>
            <Form.Control type="range" min="0" max="255" id="red" onChange={changeColor} />
          </Form.Group>
          <Form.Group controlId="formBasicRange">
            <Form.Label>Green :</Form.Label>
            <Form.Control type="range" min="0" max="255" id="green" onChange={changeColor} />
          </Form.Group>
          <Form.Group controlId="formBasicRange">
            <Form.Label>Blue :</Form.Label>
            <Form.Control type="range" min="0" max="255" id="blue" onChange={changeColor} />
          </Form.Group>
        </Form>

        <p>rgb({red}, {green}, {blue})</p>
      </div>
    </div>
  );
}